'use client'

/** React Imports */
import * as React from 'react'

/** Next Imports */
import { usePathname } from 'next/navigation'

/** MUI Imports */
import { Box, Button, ThemeProvider } from '@mui/material'

/** Style Import */
import theme from "./style"

/** Layout */
export default function RootLayout({ children }) {
  const pathname = usePathname() || ""

  async function handleLogout() {
    // discover current user id (same fallback as chat)
    let userId = null
    try {
      const raw = typeof window !== 'undefined' ? localStorage.getItem('user') : null
      if (raw) {
        const parsed = JSON.parse(raw)
        userId = parsed?.id ?? null
      }
    } catch (e) {
      userId = null
    }

    // Tell the server to mark the user offline (best-effort)
    try {
      if (userId) {
        await fetch('/api/users?operation=logout', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ id: userId }),
        }).catch(() => {})
      } else {
        // fallback to any session logout endpoint you may have
        await fetch('/api/auth/logout', { method: 'POST' }).catch(() => {})
      }
    } catch (e) {
      // ignore errors
    }

    // clear client-side stored user info used by chat/map fallback
    try {
      if (typeof window !== 'undefined') {
        localStorage.removeItem('user');
        localStorage.removeItem('userId');
        // optionally clear other tokens/cookies here
      }
    } catch (e) {
      // ignore
    }

    // Replace current history entry and do a full navigation to /uis.
    // This prevents the usual Back button from returning to the previous page.
    if (typeof window !== 'undefined') {
      window.location.replace('/uis');
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <html lang="fr">
        <body style={{ display: "flex", minHeight: "100vh", flexDirection: "column" }}>
          {/* Header with Logout on the top-right */}
          <Box
            component="header"
            sx={{
              width: '100%',
              px: 2,
              py: 1,
              borderBottom: 1,
              borderColor: 'divider',
              display: 'flex',
              justifyContent: 'flex-end',
              alignItems: 'center'
            }}
          >
            <Button
              onClick={handleLogout}
              sx={{
                color: 'error.main',
                textTransform: 'none',
                fontWeight: 600,
                '&:hover': { backgroundColor: 'transparent' }
              }}
            >
              Logout
            </Button>
          </Box>

          {/* Main content grows to fill available space */}
          <main style={{ flex: 1 }}>{children}</main>
        </body>
      </html>
    </ThemeProvider>
  )
}